<template>
	<div class="sub-container">
		<div class="page-title ">
			<h2>유튜브</h2>
			<div class="page-arrow">
				<a
					class="blackHans current-page"
					href="https://www.youtube.com/channel/UCRktbEsXzmtmTp5xCUn3MHw"
					target="_blank"
					>운동친구 유튜브 채널로 이동하기</a
				>
				<RightArrow class="arrows category-button-next" />
			</div>
		</div>
		<div class="article-list clr">
			<YoutubeItem
				v-for="youtubeItem in youtubeList"
				:key="youtubeItem._id"
				:item="youtubeItem"
			/>
		</div>
		<Pagination
			v-if="totalPage !== -1"
			:listRowCt="15"
			:pageLinkCt="5"
			:totalCount="totalPage"
			:url="'/youtube'"
		/>
	</div>
</template>

<script>
import { fetchYoutube, fetchYoutubeTotalCount } from '@/api/youtube';
import YoutubeItem from '@/views/user/youtube/components/Youtube-Item';
import RightArrow from '@/assets/icon/RightArrow.vue';
import Pagination from '@/components/common/Pagination';

export default {
	components: { YoutubeItem, RightArrow, Pagination },
	created() {
		this.fetchYoutubeTotalCount();
		let page = this.$route.params.page - 1 || 0;
		this.fetchYoutube(page);
	},
	data() {
		return {
			current: 1,
			isLoading: false,
			youtubeList: [],
			totalPage: -1,
		};
	},
	methods: {
		async fetchYoutube(page) {
			console.log(page);
			const { data } = await fetchYoutube(page);
			this.youtubeList = data;
		},
		async fetchYoutubeTotalCount() {
			this.isLoading = true;
			try {
				const { data } = await fetchYoutubeTotalCount();
				this.totalPage = data.totalCount;
			} catch (e) {
				alert(e);
			}
		},
	},
	watch: {
		$route(to, from) {
			if (to !== from) {
				this.currentPage = this.$route.params.page;
				this.fetchYoutube(this.currentPage - 1);
			}
		},
	},
};
</script>

<style lang="scss" scoped>
.page-arrow {
	text-align: center;
	vertical-align: middle;
}
.arrows {
	vertical-align: middle;
}
.category-swiper {
	width: 150px;
	display: inline-block;
	vertical-align: middle;
}

.redFont {
	background: red;
}

.msg {
	color: green;
}

.blackHans {
	font-family: Black Han Sans;
	font-size: 16px;
}
</style>
